import React, {Component, PropTypes } from 'react'
import { Link } from 'react-router';

import styled from 'styled-components';
import './Home.css'
import TextContainer from '../content/TextContainer'



export default class Home extends Component {
    static PropTypes = {};
    static homeColor = "#c678dd";

    render() {
        return (
            <div className="Content">
                <h1 style={{paddingTop:"2.0em"}}>
                    General Information
                </h1>
                <TextContainer>
                    <h2>
                    Greetings, weary traveler of the web.
                    </h2>
                    My name is Bret. I'm a hacker, musician, and a college
                    student. I am pursuing my Bachelor's degree in Computer
                    Science with a minor in Mathematics at the University of Pittsburgh. I currently maintain a 3.9 GPA.
                    I currently have 2 years of professional experience in software development, but I have
                    been hacking, coding, and contributing for much longer. Please look at the rest of this site to learn more about me,
                    or see my Github, Linkedin, Twitter, to the left.
                </TextContainer>
                <TextContainer>
                    <h2> 
                        A bit about me
                    </h2>
                    I'm a proud older brother to 3 younger siblings. In my free time, you can find me practicing bass guitar, lifting, or writing hobby code.
                    I also consider myself to be a Vim-wizard, although I don't think Vim is the end-all-be-all of text editors.
                    I love theoretical CS and math, and I often find myself reading Wikipedia articles on subjects way over my head.
                    I'm passionate about computer science and education, especially in the realm of cybersecurity. Check out the "Projects" link to see how2exploit_binary,
                    my hacking education/tutrial project on Github.
                </TextContainer>
            </div>
        );
    }
}




