import React, {Component } from 'react'
import {PropTypes} from 'prop-types'
import { Link,withRouter } from 'react-router';
import './Button.css'

class SideBarItem extends Component {

    constructor(props) {
        super(props);
        this.state = {active:false, imgSrc: props.imgSrc ,type:this.props.type, clickEvent: props.clickEvent, link: props.link,loaded:false}
    }

    getImageStyle = (imageNum) => {
        return ({
            cursor: "pointer",
            transition: "opacity 0.9s linear",
            width:"32px",
            position: "absolute",
            right: "5%",
            top:"20%",
            opacity: this.state.loaded ? 1 : 0,
        });
    }

    onLoad = () => {
        this.setState({loaded:true}) 
    }

    onClick = () => {
        (this.state.type === "arrow") ? this.state.clickEvent() : window.open(this.state.link)
        this.setState({active: !this.state.active})
    }

    toggleHover = () => {
        this.setState({hover: !this.state.hover}) 
    }

    render() {
        const divStyle = {
            position: "relative",
            overflow: "auto",
            width: "100%",
            height: "8%",
            margin: "0",
            backgroundColor: this.state.hover ? "rgba(255,255,255,0.1)" : "rgba(0,0,0,0)",
            verticalAlign: "center",
            textAlign: "center",
        }

        const arrowStyle = {
            ...this.getImageStyle(1), 
            transform: this.state.active ? "rotate(-90deg)" : "rotate(90deg)",
            transition: "transform .30s linear"
        }

        const imgStyle = (this.state.type === "arrow" ? arrowStyle : this.getImageStyle(1))
        return (
            <div style={divStyle} onMouseEnter={this.toggleHover} onMouseLeave={this.toggleHover} onClick={this.onClick}>
                <p style={{position: "absolute",  top: "10%", left:"10%"}}>{this.props.children}</p>
                <img src={this.state.imgSrc} 
                    onLoad={this.onLoad}
                    alt="" 
                    style={imgStyle}
                />
            </div>
        );
    }
    
}

export default SideBarItem



