import React, { Component } from 'react';
import TextContainer from '../content/TextContainer.js'

class Projects extends Component {
    render() {
    const projectColor = "#e06c72";
        return (
            <div className="Content">
                <h1 style={{paddingTop: "2em"}}>
                    My Repositories
                </h1>
                
                <TextContainer>
                    <h2>how2exploit_binary</h2>
                    Of the many projects that I have started, this is by far the one I am the most proud of.
                    how2exploit_binary is an education repository designed to teach people the basics of binary exploitation.
                    I started this repository in high school because I had a hard time finding the information that I needed to write eploits
                    for hacking competitions. This led me to create this repository, which has a useful bank of information, as well as a narrative-style
                    set of tutorials designed for complete beginners. I'm proud to say that this resource has over 200 stars on Github, and I have received
                    several thank-you emails from high-school CTF teams telling me how useful they found the tutorials and information.
                </TextContainer>

                <TextContainer>
                    <h2>TuringScript</h2>
                    This project is still under heavy construction, but the end
                    goal is to write a compiler from C to a Turing machine.  As
                    of right now, I have implemented a "micro-language" which
                    is a basic abstraction of the Turing Machine's behaviors,
                    plus basic branching capability. This project can be found
                    on my Github account
                </TextContainer>
                <h1>Open Source Contribution</h1>
                <TextContainer>
                    <h2>Oni</h2>
                    Oni is a Typescript/Javascript based frontend for Neovim, my text editor of choice. I, like many other vim users,
                    felt that the regular Vim experience was not enough, so I contributed to this repository.
                </TextContainer>
                <h1>Other</h1>
                <TextContainer>
                    <h2>This Website!</h2>
                    This website was written entirely by me. I used Reactjs for this one, but I didn't copy anyone else's presets or templates.
                    Everything you see in front of you is handwritten
                </TextContainer>
                    
            </div>
        );
    }
}

export default Projects;



