import React, { Component, Text } from 'react';
import Typist from 'react-typist'
import PageButton from '../content/Button'
import SideBar from '../content/Sidebar.js'
import './App.css';
import '../content/Typist.css'

    class App extends Component {
        render() {
            return (
            <div style={{height:"100%"}}>
                <div className="TopBar">
                    <div className="Name"></div>
                    <div className="MainMenu">
                        <Typist className = "Name" avgTypingDelay={150}> 
                            <Typist.Delay ms={500}/>
                                Bret Barkley
                        </Typist>
                        <PageButton label="Home" hoverColor="#c678dd" link="/"> </PageButton>
                        <PageButton label="Professional" hoverColor="#8dbb6e" link="/Professional"> </PageButton>
                        <PageButton label="Academic" hoverColor="#61afef" link="/Academic"> </PageButton>
                        <PageButton label="Projects" hoverColor="#e06c72" link="/Projects"> </PageButton>
                    </div>

                    <SideBar></SideBar>
                </div>
        {this.props.children}
            </div>
        );
    }
}


export default App;



