import React, {Component } from 'react'
import {PropTypes} from 'prop-types'
import { Link,withRouter } from 'react-router';
import './Button.css'

class PageButton extends Component {

    static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
    }
    constructor(props) {
        super(props);
        this.state = {label: props.label, activated: false, hover:false, hoverColor: props.hoverColor, link: props.link};
    }

    getStyle() {
        const { match, location, history } = this.props
        return (
            {
                //transition: "width .15s linear",
                position: "relative",
                fontSize: "100%",
                marginLeft: "auto",
                marginRight: "auto",
                marginTop: "none ",
                left:0,
                border: "none",
                borderBottom: (location.pathname === this.props.link) ? "1px solid " + this.state.hoverColor : "0px",
                padding: 0,
                display: "inline-block",
                color: (this.state.hover || location.pathname === this.props.link) ? this.state.hoverColor : "white",
                height: "100%",
                width: "16%",
                //width: this.state.hover ?"25%" :  "15%",
                cursor: "pointer", 
                fontSize: "100%",
                backgroundColor: "var(--darkerSwankyGray)",
                
                //backgroundColor : this.state.hover ? this.state.hoverColor : "var(--swankyGray)",
                boxShadow: (this.state.hover) ? "0px 12px 10px -10px " + this.state.hoverColor : null,
                outlineStyle: "none",
                outlineWidth: "none",
                animation: (this.state.hover) ? ["FadeLight", this.state.hoverColor.replace("#","")," 1.5s  ease-in-out infinite"].join('') : "b",
                //("FadeLight" + this.state.hoverColor +" 2s  ease-in-out infinite") : "none",

            }
        )
    }

    toggleHover = () => {
        this.setState({hover: !this.state.hover})
    }

    onClick = () => {
        this.setState({active: true})
    }

    render() {
        const { match, location, history } = this.props
        return (
            <Link to={this.state.link}>
                <button 
                        style={this.getStyle()}
                        onMouseEnter={this.toggleHover}
                        onMouseLeave={this.toggleHover}
                        onClick={this.onClick}
                        onFocus={null}>
                        {this.props.label}
                </button>
            </Link>
        );
    }
    
}

export default withRouter(PageButton)




