import React, {Component } from 'react'
import {PropTypes} from 'prop-types'
import { Link,withRouter } from 'react-router';
import './Button.css'
import SideBarItem from './SideBarItem.js'

class SideBar extends Component {

    constructor(props) {
        super(props);
        this.state = {active:false}
    }

    getStyle() {
        return (
            {
                fontFamily: "Arial",
                transition: "left .20s ease-in-out, width .20s ease-in-out",
                position: "absolute",
                top: "104px",
                left: this.state.active ? "3%" : "-230px",
                margin: 0,
                boxSizing: "border-box",
                padding: "0%",
                backgroundColor: "var(--darkerSwankyGray)",
                color: "white",
                textAlign: "Left",
                width:this.state.active ? "15%": "290px",
                overflow:"overlay",
                height: "800px",
            }
        )
    }

    getImageStyle = (imageNum) => {
        return ({
            width:"32px",
            position: "absolute",
            right: "4%",
            top:"20%",
        });
    }



    onClick = () => {
        this.setState({active: !this.state.active})
    }


    gtdivStyle = (num) => {
        return ({
            position: "relative",
            overflow: "auto",
            width: "100%",
            height: "8%",
            margin:0,       
        });
        
    }
    render() {
        const divStyle = {
            position: "relative",
            overflow: "auto",
            width: "100%",
            height: "8%",
            margin: "0",
        }

        const arrowStyle = {
            ...this.getImageStyle(1), 
            transform: this.state.active ? "rotate(-90deg)" : "rotate(90deg)",
            transition: "transform .20s ease-in-out"
        }
        const arrow = require('./up-white-arrow-hi.png')
        const gitMark = require('./GitHub-Mark-Light-64px.png')
        const linkedIn = require('./In-White-128px-TM.png')
        const twitter = require('./Twitter_Social_Icon_Rounded_Square_White.png')
        const resume = require('./resume-icon-png-10.png')
        const email = require('./Email.png')
        return (
            <div style={this.getStyle()} >
                <SideBarItem imgSrc={arrow} type="arrow" clickEvent={this.onClick.bind(this)}>Quick Links</SideBarItem>
                <SideBarItem imgSrc={gitMark} type="" link="https://github.com/Bretley">Github</SideBarItem>
                <SideBarItem imgSrc={linkedIn} type="" link="https://www.linkedin.com/in/bbarkley98">Linkedin</SideBarItem>
                <SideBarItem imgSrc={twitter} type="" link="https://twitter.com/barkley_bret">Twitter</SideBarItem>
                <SideBarItem imgSrc={resume} type="" link="asdf">Resume</SideBarItem>
                <SideBarItem imgSrc={email} type="" link="asdf">E-mail</SideBarItem>
            </div>
            
        );
    }
    
}

export default SideBar



