
import React, { Component } from 'react';
import "./Home.css"
import TextContainer from '../content/TextContainer'

class Academic extends Component {
    static academicColor = "#61afef";

    render = () => {
        return (
            <div className="Content">
                <h1 style={{paddingTop: "2.0em"}}>Education</h1>
                <TextContainer>
                    <h2>Bachelor's in Computer Science, University of Pittsburgh (Student) </h2>
                    I am a rising Junior (by credit) at the University of Pittsburgh's School of Computing and Information.
                    I take my studies very seriously, and I currently have a 3.92 cumulative GPA. I intend to get a concentration in 
                    high performance computing, which entails upper level courses on compiler design, computer architecture, networking, and operating systems.
                    I also currently intend to supplement my Computer Science degree with a minor in mathematics. I intend to take courses on graph theory, combinatorics,
                    linear algebra, and analysis.
                </TextContainer>
                <TextContainer>
                    <h2>Diploma, Thomas Jefferson High School</h2>
                    I received my diploma from the West Jefferson Hills School District in the 2016-2017 school year. In high school, I challenged myself
                    by taking AP and Honors courses in history, physics, english, mathematics, and computer science. I received a 3.7 unweighted GPA and finished
                    in the top 15% of my class.
                </TextContainer>
                <h1>Research</h1>
                <TextContainer>
                    <h2 style={{textAlign:"left"}}> Research at CyRen Lab </h2>
                    In addition to my studies in the classroom, I also
                    conduct research at CyREN Lab (insert link), a Cybersecurity research group that
                    sets out to empirically test "common knowledge" and "best
                    practices" in the Cybersecurity space, using virtualization and camouflage to mimic real networks. At the lab,
                    I predominantly serve in a jack-of-all trades position, assisting the Systems team and Data Analytics team. I also am
                    scpecifically responsible for penetration testing of the networks we develop.
                </TextContainer> 
                <h1>Academic Accomplishments</h1>
                <TextContainer>
                    <h2>3rd Place, PicoCTF</h2>
                    PicoCTF, a national CyberSecurity contest for high
                    schoolers, covers a wide range of problems in
                    CyberSecurity. My team placed 3rd out of roughly 8,000
                    teams nationally. I personally handled problems in Reverse Engineering and Binary Exploitation, exploiting vulnerabilities
                    such as Use-After-Free, Double-Free, and Buffer-Overflow. I personally was quite happy with this result, especially considering our team only
                    had 4 members compared
                </TextContainer>
                <TextContainer>
                    <h2>Top 10 Finalist, CSAW HSF</h2>
                    HSF is a national CyberForensics competition for high schoolers hosted by NYU. My team was a top 10 finalist, which resulted in us flying to New York to
                    compete in the final competition at NYU
                </TextContainer>
                <TextContainer>
                    <h2>Computer Science Department Award, TJHS</h2>
                    In addition to receiving my diploma from Thomas Jefferson High School, I also received the Department Award for Computer Science,
                    an award given a single student in the graduating class who excelled in a specific discipline.
                </TextContainer>
            </div>
        );
    }
}

export default Academic;



