import React, { Component } from 'react';
import TextContainer from '../content/TextContainer.js'

class Professional extends Component {
    static professionalColor = "#8dbb6e";

    render() {
        return (
            <div className="Content">
                <h1 style={{paddingTop:"2.0em"}}>Previous Employment</h1>
                <TextContainer>
                <h2>Pittsburgh Supercomputing Center</h2>
                <p><b>Position: </b>DevOps Assistant</p>
                <p><b>Time: </b> ~2 Years (2016 - 2018)</p>
                <p><b>Responsibilities: </b> 
                    At the Supercomputing Center, I was primarily in charge of writing scripts for 
                    reporting and data visualization. I compiled energy and consumption data into a visualization based on the physical
                    layout of the supercomputers. I also used Perl to interface with an SQL database to automatically generate Excel spreadsheets
                    containing information about resource utilization and waste. I also gained some experience setting up ElasticStack environments to 
                    better visualize log data from syslog, databases, and dumped CSVs
                </p>
                <p><b>Skills: </b> Python, SQL, Perl, Data Visualization (using pandas and plotly librariees), ElasticStack </p>
                </TextContainer>
                <TextContainer>
                <h2>OfficeDepot</h2>
                <p><b>Position: </b>Sales Associate</p>
                <p><b>Time: </b>~2 Years (2014 - 2016)</p>
                <p><b>Responsibilities: </b>
                    At OfficeDepot, I was responsble for ensuring customer satisfaction and also maintaining inventory for roughly 1/4th of the store.
                    My most common role was working the cash register, and being the last point of contact with the customer before they left the store. Consequently,
                    I was generally the one accountable for a majority of the customer reviews that came from my shift. 
                </p>
                <p><b>Skills: </b>
                    Customer Interaction, Communication, Organization
                </p>

                </TextContainer>
            </div>
        );
    }
}

export default Professional;



