import React, {Component } from 'react'
import {PropTypes} from 'prop-types'
import { Link,withRouter } from 'react-router';
import styled from 'styled-components';



class TextContainer extends Component {
    static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
    }

    getColor = () => {
        const { match, location, history } = this.props;
        if (location.pathname === "/") {
            return "rgba(198,120,221,0.5)";
        } else if (location.pathname === "/Professional") {
            return "rgba(141,187,110,0.5)";
        } else if (location.pathname === "/Academic") {
            return "rgba(97,175,239,0.5)";
        } else if (location.pathname === "/Projects") {
            return "rgba(224,108,114,0.5)";
        }
    }

    getStyle = () => {
        return ({
            fontSmooth: "always",
            color: "rgba(255,255,255,0.9)",
            backgroundColor: "rgba(0,0,0,0.05)",
            border: "5px groove " + this.getColor(),
            whiteSpace:"pre-wrap",
            padding: "2%",
            paddingTop:"1%",
            margin: "auto",
            marginBottom: "2%",
            textAlign: "Left",
            width: "55%", 
        });
    }

    render() {
        return (
            <div className="" style={this.getStyle()}>
                {this.props.children}
            </div>
        );
    }
}


export default withRouter(TextContainer)



